.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Responsive-width {
  @apply w-full max-w-sm sm:max-w-sm md:max-w-md lg:max-w-lg;
}

.heart {
  position: absolute;
  font-size: 2rem;
  animation: fly 1s linear forwards;
  opacity: 0;
}

@keyframes fly {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: translateY(-2000px);
    opacity: 1;
  }
}